
*{-webkit-box-sizing:border-box;box-sizing:border-box;
}
i,b{font-style:normal;font-weight:400;
}
html,body{
    width:100%;
    height:100%;
}
#app {
    width:100%; 
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
     -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50; 
    padding:30px 0;
}
.flex{
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-pack:justify;
        -ms-flex-pack:justify;
            justify-content:space-between;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
}
.type{
    width:100%; 
    -webkit-box-align:center; 
        -ms-flex-align:center; 
            align-items:center;
    padding:0 30px;
}
.type span{
    display:inline-block;
    width:30%;   
    font-size:20px;
}
ul,li{
    margin:0;
    padding:0;
}
li{
    width:50%;
    display:inline-block;
    font-size:18px;
    color:#ddd;
    list-style:none;
    text-align: left;
    padding-left:5%;    
    margin-bottom:10px;
    position:relative;
}
li i{
    font-size:14px;
}
li .btns{
    display:inline-block;
    width:30px;
    height:30px;
    border:1px solid #CB4B16;
    border-radius:5px;
    line-height:30px;
    text-align: center; 
    font-weight:400;
}
li .btna{
    background: #CB4B16 ;
    vertical-align:middle; 
    color:#CB4B16;
}
li .ddd{
    color:#ddd!important;
}
li .order{
    display:inline-block;
    width:25px;
}
.clock{
    padding:20px 0 ;
}
.time{
    color:#ddd;
}
.addErr{
    position:fixed;
    top:15%;
    right:1%;
    width:50px;
    height:50px;
    line-height:50px;
    text-align: center; 
    font-size:20px;
    color:#fff;
    background:#B58900;
    z-index:100;
    border-radius:50%;
}















































































